.navbar{
  transition: .4s ease-in-out;
}

.modebtns{
  display: grid;
  margin: auto 20px;
  place-items: center;
}

.form-control{
  transition: all .3s ease-in-out;
}

.bx{
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 25px;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  border: 1px solid #ccc;
  transition: .3s ease-in-out;
}

.bx-moon:hover{
  background-color: white;
  color: black;
}

.bx-sun{
  color: black;
  border: 1px solid #777;
}

.bx-sun:hover{
  color: white;
  background-color: black;
}
